  border-style: solid;
  border-width: 1px;
  border-radius: 15px;
  margin-top: 20px;
}

.text-h1 {
  font-family: "Sk-Modernist-Bold";
  font-size: 36px;
  margin-top: -10px;
  margin-bottom: 0px;
}

.image-col {
  position: relative;
}

.img-profile {
  width: 120px;
  height: 120px;
  overflow: hidden;
  border-radius: 100px !important;
  margin: 50px auto 0;
}
.img-profile img {
  object-fit: cover;
  object-position: top;
}

.img-camera-default {
  width: 35px;
  height: 35px;
  position: absolute;
  bottom: 5px;
  right: 110px;
}

ion-item {
  --background: var(--ion-color-blanco);
  --color: var(--ion-color-negro);
  --color-focused: var(--ion-color-negro);
  --border-width: 1px !important;
  --padding-top: 3px;
  --padding-start: 0px;
  --highlight-color-invalid: none;
  --highlight-color-valid: none;
  --highlight-color-focused: none !important;
}
ion-item.item-shape-round {
  border-style: solid !important;
  border-width: 1px !important;
  border-radius: 15px !important;
}

ion-item::part(native) {
  border: none;
}

.item-highlight {
  border-width: 0px !important;
}

.camposLabel-CItem {
  font-family: "Sk-Modernist-Regular";
  font-size: 16px;
}
.camposLabel-CItem.label-floating {
  font-family: "Sk-Modernist-Regular";
  font-size: 17px;
  color: var(--ion-color-negro) !important;
  max-width: -moz-fit-content !important;
  max-width: fit-content !important;
  margin-top: -6px;
}

.input-CItem {
  font-family: "Sk-Modernist-Regular";
  font-size: 19px;
}

.cal-Bt {
  display: block;
  font-family: "Sk-Modernist-Bold";
  font-size: 13px;
  text-transform: none;
  width: 100%;
  height: 60px;
  --border-radius: 10px;
  --box-shadow:none;
}

.img-cal {
  width: 25px;
  height: 25px;
  margin-right: 10px;
}

.date-cal {
  margin-left: auto;
  margin-right: 0;
}

